import React from 'react';
import { Card, Table, Tag, Tooltip, message, Button, Col, Row, Popconfirm, } from 'antd';
import { EyeOutlined, DeleteOutlined } from '@ant-design/icons';
import { connect } from 'react-redux';
import axios from 'axios';

const OrderList = (props) => {
    let { user_id, history, match } = props;
    let [loading, setLoading] = React.useState(true);
    let [data, setData] = React.useState([]);
    let { order_token } = match.params;

    const getOrders = async () => {
        try {
            let res = await axios.post('/orderlist', { user_id, order_token });
            setData(res.data.data)
            setLoading(false);
        } catch (error) {
            setLoading(false);
            console.log(error);
            error.response ?
                message.success(error.response.data)
                :
                message.error(error.message)
        }
    }

    const deleteOrder = async (order_id) => {
        try {
            let res = await axios.post('/deleteorder', { user_id, order_id });
            message.success(res.data.message);
            getOrders()
        } catch (error) {
            console.log(error);
            error.response ?
                message.error(error.response.data)
                :
                message.error(error.message)
        }
    }

    const updateOrderStatus = async (order_token, orderstatus_id) => {
        // try {
        //     await axios.post('/updateorderstatusagent', { user_id, order_token, orderstatus_id });
        //     getDeals();
        // } catch (error) {
        //     console.log(error);
        //     error.response ?
        //         message.error(error.response.data)
        //         :
        //         message.error(error.message)
        // }
    }

    React.useEffect(() => {
        getOrders();
    }, []);

    const columns = [
        {
            title: 'Reference No.',
            dataIndex: 'order_token',
            key: 'order_token',
            align: 'center',
            render: text => <span>
                <Tag >
                    {text}
                </Tag>
            </span>,
        },
        {
            title: 'Design Name',
            dataIndex: 'order_designname',
            key: 'order_token',
            align: 'center',
        },
        {
            title: 'Design Type',
            dataIndex: 'order_designtype',
            key: 'order_token',
            align: 'center',
        },
        {
            title: 'Work By',
            dataIndex: 'user_name',
            key: 'user_name',
            align: 'center',
            render: text => {
                return <span>
                    {text || '-'}
                </span>
            },
        },
        {
            title: 'Status',
            dataIndex: 'order_status',
            key: 'order_status',
            align: 'center',
            render: text => {
                let color = '';
                if (text === 'Pending') color = 'geekblue'
                else if (text === 'Assigned') color = 'blue'
                else if (text === 'In Progress') color = 'purple'
                else if (text === 'Completed') color = 'cyan'
                else if (text === 'Back To Agent') color = 'volcano'
                else if (text === 'Agent Fixed') color = 'orange'
                else if (text === 'Back To Manager') color = 'gold'
                else if (text === 'Edit By Client') color = 'red'
                else if (text === 'Assign Edit') color = 'magenta'
                return <span>
                    <Tag color={color} >
                        {text}
                    </Tag>
                </span>
            },
        },
        {
            title: 'Action',
            key: 'action',
            align: 'center',
            render: (text, record) => (
                <div >
                    <Tooltip title="Edit">
                        <Button
                            type="primary"
                            className="mr-2"
                            icon={<EyeOutlined />}
                            size="small"
                            onClick={() => history.push(`/orders/${order_token}/${record.order_id}`)}
                        />
                    </Tooltip>
                    {
                        record.order_status === 'Pending' &&
                        <Popconfirm
                            placement="topRight"
                            title={'Are you sure, you want to delete this? '}
                            onConfirm={() => deleteOrder(record.order_id)}
                            okText="Yes"
                            cancelText="No"
                        >
                            <Tooltip title="Remove">
                                <Button danger icon={<DeleteOutlined />} size="small" />
                            </Tooltip>
                        </Popconfirm>
                    }
                </div>
            )
        },
    ];

    return (
        <>
            <Row style={{ justifyContent: 'center' }}>
                <Col lg={22}>
                    <Card bodyStyle={{ 'padding': '0px' }}>
                        <Table
                            columns={columns}
                            dataSource={data}
                            loading={loading}
                            rowKey='order_id'
                        />
                    </Card>
                </Col>
            </Row>
        </>
    )
}

const mapStateToProps = state => ({
    user_id: state.auth.user.user_id,
    role_id: state.auth.user.role_id,
});

export default connect(mapStateToProps)(OrderList);